.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: rgba(51, 51, 51, 0.6); /* semi-transparent background */
  display: flex;
  justify-content: center; /* horizontal center */
  align-items: center; /* vertical center */
  padding: 5px 0; 
  z-index: 1000; /* force navbar above the image */
}

.navbar a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  margin-inline: 30px;
  padding: 10px;
  text-decoration: none;
}

.navbar a:hover {
  color: rgb(241, 222, 15);
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.content {
  padding: 16px;
}

.centered {
  display: flex;
  flex-direction: column; 
  align-items: center; 
  justify-content: center; 
  color: white;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 20px;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center; 
}

.centered p {
  margin: 5px 0; 
}

img {
  object-fit: cover;
  width: 100%;
  height: 100vh; /* viewport height */
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
